import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles((theme) => ({
  map: {
    maxHeight: "100%",
    maxWidth: "100%",
    border: "0"
  },
}));

const Ubicacion = () => {
  const classes = useStyles();
  const loading = "lazy", src = "https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3784.7975615077935!2d-70.28605479300371!3d-18.447498417990914!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMTjCsDI2JzUxLjEiUyA3MMKwMTcnMDkuOCJX!5e0!3m2!1ses-419!2scl!4v1619119999271!5m2!1ses-419!2scl"
  return (
    <div>
      <iframe className={classes.map} title="Map" src={src} loading={loading} width="400" height="400"></iframe>
    </div>
  );
}

export default Ubicacion;